import { AbstractControl, ValidationErrors, ValidatorFn } from '@angular/forms';

export function urlValidator(): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    if (
      control.value === null ||
      control.value === undefined ||
      control.value === ''
    ) {
      return null;
    }

    // expresso regular para validar URLs
    const urlRegex = /^(ftp|http|https):\/\/[^ "]+$/;

    // verifica se a URL corresponde  expresso regular
    if (!urlRegex.test(control.value)) {
      // se a URL for invlida, retorne uma chave 'invalidUrl' com valor verdadeiro
      return { invalidUrl: true };
    }

    // se a URL passar por todas as verificaes acima, ela  vlida
    return null;
  };
}
